import React from 'react';
import TextField from '@mui/material/TextField';
import Navbar from '../components/Navbar';
import InputAdornment from '@mui/material/InputAdornment';
import { BsArrowRightCircle } from "react-icons/bs";
import { Link } from 'react-router-dom';

const Pre = () => {
    return (
        <>
            <Navbar />
            <div className="container mt-5">
                <div className="row">
                    <div className="col-sm-9 mt-5 pt-5">
                        <h3>Phone Number</h3>
                        <p className='text-muted' style={{ fontSize: "13px" }}>A hair expert will call you when you start the plan.</p>
                        <TextField id="standard-basic"
                            type="number"
                            className='col-sm-6 mt-5'
                            label="what Is YourPhone Number"
                            variant="standard"
                            required
                            InputProps={{
                                endAdornment: <InputAdornment position="end"> <Link to="/"> <BsArrowRightCircle className='m-1' style={{ color: "#7CB9E8" }} size={25} /></Link></InputAdornment>,
                            }}
                            focused
                        />
                        <br></br>
                        <Link to="/test" style={{ color: "#7CB9E8" }}>Previous</Link>

                    </div>
                </div>
            </div>
        </>
    )
}

export default Pre