import React from 'react';
import "./otp.css";
import mobile from "../images/mobile.svg"
import { Link } from 'react-router-dom';

const Otp = () => {
    return (
        <>
            <div class="container mt-5">
                <div class="row justify-content-md-center">
                    <div class="col-md-4 ">
                        <div class="row">
                            <div class="col-sm-12 mt-5 bgWhite p-3 " style={{ borderRadius: "20px" }}>
                                <div className='text-center p-3'>
                                    <img src={mobile} height={140} alt="" />
                                </div>
                                <div class="title">
                                    <h3 className='m-0 text-center'>Verify OTP</h3>
                                </div>

                                <form action="" class="mt-2 col-sm-7 ms-auto me-auto ">
                                    <input class="otp m-1 " type="text" oninput='digitValidate(this)' style={{ borderRadius: "4px", borderColor: "lightgray !important" }} />
                                    <input class="otp m-1 " type="text" oninput='digitValidate(this)' style={{ borderRadius: "4px", borderColor: "lightgray !important" }} />
                                    <input class="otp m-1 " type="text" oninput='digitValidate(this)' style={{ borderRadius: "4px", borderColor: "lightgray !important" }} />
                                    <input class="otp m-1 " type="text" oninput='digitValidate(this)' style={{ borderRadius: "4px", borderColor: "lightgray !important" }} />
                                    <Link to="/otp" className='text-decoration-none ' style={{ fontSize: "12px", color: "#7CB9E8" }}>Resend It</Link>
                                    <button class='btn btn-primary btn-block mt-3 mb-4 customBtn w-100'>Verify</button>

                                </form>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Otp