import React from 'react';
import thanks from "../images/thankyou.svg"

const Thankyou = () => {
    return (
        <>
            <div className="container mt-5">
                <div className="row">
                    <div className="col-sm-6 ms-auto me-auto ">
                        <div className='text-center'>
                            <img src={thanks} height="300" className='w-100' alt="" />
                        </div>
                        <h2 className='text-hero-bold text-dark text-center'>Congratulations</h2>
                        <p className='text-hero-regular text-center'>
                            Your Test Completed
                        </p>

                    </div>
                </div>
            </div>


        </>
    )
}

export default Thankyou