import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import { Button } from '@mui/material';
import Divider from '@mui/material/Divider';
import Chip from '@mui/material/Chip';
import { Link } from 'react-router-dom';
// import { loginUser } from "https://alternative-medicine-revo.herokuapp.com/login";
import { useNavigate } from "react-router-dom";
import login from "../images/login.gif"




const Login1 = () => {

    const [number, setNumber] = useState("")
    const [password, setPasword] = useState("")
    const navigate = useNavigate()
    const [error, setError] = useState('')




    const loginuser = async (e) => {
        e.preventDefault()
        const login = await fetch("https://alternative-medicine-revo.herokuapp.com/login", {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ mobile: number, password: password })
        })
        let res = await login.json()
        try {
            setError(Object.keys(res)[0] + res[Object.keys(res)[0]])
            navigate('/')
        }
        catch (error) {
            console.log(error)
            setError(res[Object.keys(res)[0]] + [Object.keys(res)[0]][0])
        }




        console.log(login)
        if (login.status == 200) {

            console.log("hello working 1")

            // let res = await login.json()


            console.log("hello working")
            localStorage.setItem("auth_token", res.token);
            navigate("/");
        } else {
            let res = await login.json()
            console.log(Object.keys(res)[0])

            console.log(res[Object.keys(res)[0]][0])


            console.log("fail")
        }



    }



    return (
        <>
            <div className="container-fluid " >
                <div className="row">
                    {/* <div className='mb-5'></div> */}
                    <form action="" className="mt-2 " onSubmit={loginuser}>
                        <div className="row">
                            <div className="col-md-6">
                                <img src={login} alt="" />
                            </div>
                            <div className="col-md-4 ms-auto me-auto mt-5  p-5 ">

                                <h6 className='text-center mb-3'>Hey! Welcome Back</h6>
                                <label style={{ fontSize: "13px" }} className="mb-2">Enter Your Verified Number </label>

                                <TextField id="outlined-basic" className='w-100 mb-3' label="Enter Phone Number" variant="outlined"
                                    type="number"
                                    name='mobile'
                                    onChange={e => setNumber(e.target.value)}


                                />
                                <label style={{ fontSize: "13px" }} className="mb-2">Enter Your Correct Password</label>
                                <TextField id="outlined-basic" className='w-100 mb-3' type="password" label="Enter Password" variant="outlined"
                                    name='password'
                                    helperText="Enter Your Password For Login"
                                    onChange={e => setPasword(e.target.value)}
                                />

                                <Button variant='contained' type='submit' className='w-100 p-2' style={{ backgroundColor: "#7CB9E8" }}>Login</Button>

                                <Divider className='mt-5'>
                                    <Chip label="OR" />
                                </Divider>
                                <div className="row p-2">
                                    <div className="col-sm-6">
                                        <p style={{ fontSize: "13px" }}>  Don't Have Account</p>
                                    </div>
                                    <div className="col-sm-6 text-right">
                                        <Link to="/register" style={{ fontSize: "13px", color: "#7CB9E8" }}>SignUp</Link>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </>
    )
}

export default Login1