import React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import { Link, useNavigate } from 'react-router-dom';
// import MenuIcon from '@mui/icons-material/Menu';
// import logo from "../images/alternative-medicine.png"
import AOS from "aos";
import "aos/dist/aos.css";
import { useEffect, useState } from 'react';

import "./navbar.css";

const Navbar = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const navigate = useNavigate()

  const logOut = () => {
    localStorage.clear();
    // window.location.href = "/login";
    navigate("/login1");
  };
  useEffect(() => {
    AOS.init(
      {
        duration: 1000
      }
    );
    AOS.refresh();
    let authToken = localStorage.getItem("auth_token");
    if (!authToken) {
      setIsLoggedIn(false);
    } else {
      setIsLoggedIn(true);
    }
  }, []);
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-white sticky-top" data-aos={"fade-down"}>
        <div className="container">
          <Link className="navbar-brand fw-500" to="/" style={{ fontSize: "25px" }}> <span style={{ color: "#7CB9E8", fontSize: "25px" }}>Alternative</span> Medicine </Link>
          <button className="navbar-toggler border-0 outline-none" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <i className="fa-solid fa-bars"></i>
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav ms-auto">
              <li className="nav-item">
                <a className="nav-link active fw-bold" aria-current="page" href="/" style={{ fontsize: "13px" }}>Home</a>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/" style={{ fontsize: "13px" }}>Services</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/" style={{ fontsize: "13px" }}>About Us</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/" style={{ fontsize: "13px" }}>Contact Us</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/test" style={{ fontsize: "13px" }}>Take Health Test</Link>
              </li>
            </ul>
            &nbsp;
            {isLoggedIn ? (
              <div>
                {/* <NavLink to="/profile">
                  <button className='btn btn-dark text-light'>account</button>
                </NavLink>
                 */}
                <Link to="">
                  <img
                    src="https://t3.ftcdn.net/jpg/03/46/83/96/360_F_346839683_6nAPzbhpSkIpb8pmAwufkC7c5eD7wYws.jpg"
                    className="mb-0"
                    style={{ height: "40px" }}
                    alt=""
                    id="basic-button"

                  />
                </Link>
                <Button onClick={logOut} >Logout</Button>
              </div>

            ) : (
              <div>
                {/* <Button className='mat  ' variant="contained" size="small" color="inherit">< FiLogIn size={15} />&nbsp;Login</Button> */}
                <Link to="/login1" className='btn btn-primary p-4 py-2 ms-2' style={{ borderRadius: "3px" }}>Login</Link>

                {/* <Button colorScheme='teal' size='xs'>
                                Button
                            </Button> */}
              </div>
            )}
          </div>

        </div>
      </nav>
    </>
  )
}

export default Navbar