import React from 'react';
import mobile from "../images/mob.png"

const Cunsult = () => {
  return (
    <>
      <div className="container mt-5">
        <div className="row">
          <div className="col-sm-7">
            <img src={mobile} alt="" height="100" className='ms-auto me-auto' style={{ height: "360px" }} />
          </div>
          <div className="col-sm-5">
            <h4>Download the Alternate Medicine app</h4>
            <p className='text-muted' style={{ fontSize: "12px" }}>Access video consultation with India’s top doctors on the Practo app.<br></br> Connect with doctors online, available 24/7, from the comfort of your home.</p>
            <div className='mt-5 col-sm-12'>
              Get the link to download the app
              <div class="mb-3 mt-2">
                {/* <label for="exampleInputEmail1" class="form-label">Email address</label> */}
                <div className="row">
                  <div className="col-sm-12 d-flex">
                    <input type="email" class="form-control p-2 w-50" style={{ borderRadius: "0px" }} id="exampleInputEmail1" aria-describedby="emailHelp" />
                    <input type="button" value="Get Link" style={{ backgroundColor: "#7CB9E8" }} class="form-control p-2 w-25 ms-2 text-light "  id="exampleInputEmail1" aria-describedby="emailHelp" />
                  </div>
                  {/* <div className="col-sm-5">
                    <button className='p-2 text-light me-2 px-4' style={{ backgroundColor: "#7CB9E8" }}>Get Link</button>
                  </div> */}
                  <div className="col-sm-12 d-flex mt-4">
                    <img src="https://www.practostatic.com/consumer-home/desktop/images/1597423628/google_play.png" style={{ height: "40px" }} alt="" />
                    <img src="https://www.practostatic.com/consumer-home/desktop/images/1597423628/apple_store.png" className='ms-2' style={{ height: "40px" }} alt="" />
                  </div>
                  <div className="col-sm-6 mt-4">
                    {/* <img src="https://www.practostatic.com/consumer-home/desktop/images/1597423628/apple_store.png" style={{ height: "40px" }} alt="" /> */}
                  </div>
                </div>
                {/* <div id="emailHelp" class="form-text">We'll never share your email with anyone else.</div> */}
              </div>

            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Cunsult